.section {
    margin:          0.5em 2em;
    display:         flex;
    flex-direction:  column;
    justify-content: center;
    align-items:     center;
}

.section-title {
    font-size: 0.9em;
    margin-bottom: 1em;
    display: flex;
    justify-content: flex-start;
    width: 100%;
}

.section-title p {
    color:        var(--primary-font-color);
    font:         var(--secondary-font);
    position: relative;
    display: inline-block;
}

.section-title p::after {
    content:    '';
    position:   absolute;
    left:       0;
    top:        110%;
    height:     1px;
    width:      100%;
    background: orange;
    box-shadow: 0px 1px 1px 1px lightgrey;
    opacity:    50%;
}


@media (max-width: 576px) {
    .section {
        margin: 0.5em 1em;
    }
}


.timeline {
    position:        relative;
    display:         flex;
    flex-direction:  row;
    flex-wrap:       wrap;
    /* border:       1px solid red; */
    /* margin-right: 2em; */
    margin-right:          1em;
}

/* vertical bar */
.timeline::after {
    content:    '';
    height:     calc(100% - 9em);
    position:   absolute;
    top:        2.5em;
    left:       100%;
    width:      1px;
    background: var(--primary-aux-color);
    box-shadow: 0px 0px 3px 1px rgba(15, 160, 138, 0.3);
}

.timeline-item {
    display:         flex;
    flex-direction:  row;
    flex-wrap:       wrap;
    justify-content: space-between;
    align-items:     center;
    line-height:     20px;
    font-weight:     400;
    font-size:       0.7em;
    border-radius:   0.2em;
    position:        relative;
    padding:         0.7em;
    margin:          1em;
    margin-right:    2em;
    box-shadow:      rgba(50, 50, 93, 0.25) 0px 2px 5px -1px,
                     rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
}

/* top border */
.timeline-item::before {
    content:                 '';
    position:                absolute;
    left:                    0;
    top:                     0;
    width:                   100%;
    height:                  5px;
    border-top-right-radius: 0.2em;
    border-top-left-radius:  0.2em;
    background:              var(--primary-aux-color);
}

.timeline-item * {
    padding: 0 0.3em;
    margin: 0.5em 0;
}


.timeline-item .date {
    position:       absolute;
    display:        inline-block;
    align-self:     flex-end;
    line-height:    1.5;
    margin-right:   0.3em;
    padding:        .20rem .5rem;
    border:         1px solid transparent;
    border-radius:  .25em;
    position:       relative;
    border:         1px solid #ced4da;
    box-shadow:     rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    display:        inline-block;
    vertical-align: middle;
}


.timeline-item .title {
    position: relative;
    color: var(--primary-aux-color);
    font-weight: 700;
    font-size: 16px;
}

.timeline-item .title::after,.title::before {
    visibility: hidden;
}

/* pointing prism */ 
.timeline-item .title::after,.date::after {
    content:                 '';
    position:                absolute;
    left:                    calc(100% + 10px);
    top:                     calc(50% - 5px);
    width:                   10px;
    height:                  10px;
    transform:               rotate(-135deg) skew(10deg, 10deg);
    border-bottom: 1px solid rgba(50, 50, 93, 0.25);
    border-left: 1px solid rgba(50, 50, 93, 0.25);
    background: var(--secondary-bg-color);
}

/* bar dot */ 
.timeline-item .title::before,.date::before {
    content:                 '';
    position:                absolute;
    left:                    calc(100% + 3em - 2px);
    top:                     calc(50% - 3px);
    transform:               rotate(45deg);
    width:                   6px;
    height:                  6px;
    background: var(--primary-aux-color);
    z-index: 99;
}


.timeline-item .location {
    color: var(--primary-font-color);
    font: var(--secondary-font);
    width: 100%;
}

.timeline-item .description {
    color: var(--secondary-font-color);
    font: var(--secondary-font);
    width: 100%;
}

@media (min-width: 576px) {
    .timeline {
        display: grid;
        grid-template-columns: 1fr 1fr;
        margin: 0;
    }
    .timeline::after {
        height: calc(100% - 9.5em);
        left: 50%;
        top:  2em;
    }
    .timeline-item {
        align-self: start;
        margin-right: 1.5em;
        grid-row: span 2;
    }
    .timeline-item:first-child {
        grid-column: 2;
    }
    .timeline-item:nth-child(odd) {
        margin-left: 1.5em;
    }
    .timeline-item:nth-child(even) {
        margin-right: 1.5em;
    }
    .timeline-item:nth-child(odd) .title::after {
        left:  -1em;
        visibility: visible;
        transform: rotate(45deg) skew(10deg, 10deg);
    }
    .timeline-item:nth-child(odd) .title::before {
        left:  calc(-2em - 1px);
        visibility: visible;
    }
    .timeline-item:nth-child(odd) .date::before {
        visibility: hidden;
    }        
    .timeline-item:nth-child(odd) .date::after {
        visibility: hidden;
    }        
    .timeline-item:nth-child(even) .date::before {
        left: calc(100% + 33.5px);
    }        
}

.contact {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
}

.contact-form {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    /* align-items: flex-start; */
    width: min(400px, 100% - 2em);
    box-shadow: 
        rgba(50, 50, 93, 0.25) 0px 2px 10px 0px, 
        rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
    border-radius: 0.4em;
    padding: 0.9em 1em 0.6em 1em;
}

.contact-form * {
    margin: 0.5em 0em;
}

.contact-form * {
    font-family:      var(--primary-font);
    font-size:        0.8em;
}

input, textarea {
    display:          block;
    width:            100%;
    padding:          .375rem .75rem;
    font-size:        1rem;
    line-height:      1.5;
    color:            var(--secondary-font-color);
    background-color: #fff;
    background-clip:  padding-box;
    border:           1px solid #ced4da;
    border-radius:    .25rem;
    transition:       all .15s ease-in-out;
    box-sizing:       border-box;
}

input:focus, textarea:focus {
    box-shadow: var(--primary-aux-color) 0px 0px 3px;
    outline: none;
}

textarea {
  height: 200px;
  resize:none;
}


button {
    position:        relative;
    box-sizing:      border-box;
    text-decoration: none;
    display:         inline-block;
    font-weight:     400;
    text-align:      center;
    white-space:     nowrap;
    vertical-align:  middle;
    -webkit-user-select:     none;
        -ms-user-select:     none;
            user-select:     none;
    border:          1px solid transparent;
    padding:         .375rem .75rem;
    font-size:       1rem;
    line-height:     1.5;
    border-radius:   .25rem;
    transition:      all .15s;
    color:           #fff !important;
    background:      var(--primary-aux-color);
    border-color:    var(--primary-aux-color);
    cursor:          pointer;
    outline:         none;
    background-position: center;
    transition: background 0.4s;
}

button:hover {
    color:      var(--primary-aux-color) !important;
    background: transparent !important;
}

button:after {
  content: "";
  background: transparent;
  display: block;
  position: absolute;
  width: 100%;
  height:  100%;
  border-radius:   .25rem;
  top:  0;
  left:  0;
  transform: scale(1.1);
  transition: all 0.6s;
  transform-origin: 0 100%;
}

button:active:after {
  background: var(--primary-aux-color);
  transform: scale(0);
  transition: 0s
}

button:focus, button:focus:hover {
  color:      var(--primary-aux-color) !important;
  background: transparent !important;
}

.alert {
    display:          flex;
    flex-wrap:        none;
    align-items:      center;
    justify-content:  space-between;
    font-size:        1em;
    font-weight:      400;
    line-height:      1.5;
    text-align:       left;
    box-sizing:       border-box;
    position:         relative;
    padding:          .75rem 1.25rem;
    border:           1px solid transparent;
    border-radius:    .25rem;
    color:            #155724;
    background-color: #d4edda;
    border-color:     #c3e6cb;
    margin-bottom:    0;
    margin-top:       0.3em;
    height:           2em;
}

.alert img {
    fill:             none;
    stroke-opacity:   0;
    height:           200%;
}

.failure {
    color:            #721c24;
    background-color: #f8d7da;
    border-color:     #f5c6cb;
}

.loading {
    color:            #004085;
    background-color: #cce5ff;
    border-color:     #b8daff;
}

.success {
    color:            #155724;
    background-color: #d4edda;
    border-color:     #c3e6cb;
    justify-content:  center;    
}




.header {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    font-size: 1em;
}

.profile-picture img {
  margin-top: 4em;
  height: var(--picture-size);
  border-radius: calc(var(--picture-size) / 2);
  box-shadow: 
    rgba(14, 30, 37, 0.12) 0px 2px 4px 0px, 
    rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
}


.info h1 {
    font: var(--secondary-font);
    color: var(--primary-font-color);
    margin-top: 2em;
    font-size: 1em;
    font-weight: 400;
}

.info p {
    margin-top:  0.6em;
    font-size: 0.85em;
}

.navbar {
    margin-top: 3em;
    font-size: 0.6em;
}

.navbar ul {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    font-size: 1.2em;
}

.nav-link {
    margin: 0 0.5em;
    font-size: 1em;
    position: relative;
    font-weight: 700;
}

.nav-link::after{
    content: '';
    position: absolute;
    left: 0;
    top: 100%;
    width: 100%;
    height: 2px;
    background: var(--primary-aux-color);
    opacity: 0;
    transition: all 0.3s ease-in;
}


.nav-link:hover::after{
    opacity: 1;
}

.media {
    margin: 3em;
    color: var(--primary-font-color);
}

.media ul {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    font-size: 0.9em;
}

.media-item {
    padding:     0em 0.8em;
    display:     flex;
    align-items: center;
    position:    relative;
}

.media-item:hover .tooltip {
    transform: scale(1) translateX(-50%);
    transition: transform 0.3s ease;
    transform-origin: 0 100%;
}

.tooltip {
      position: absolute;
      top: -200%; 
      left: 50%;
      transform: scale(0) translateX(-50%);
      width: -webkit-max-content;
      width: -moz-max-content;
      width: max-content;
      border-radius: 0.3em;
      padding: 0.5em;
      box-shadow: 
        rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, 
        rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
      color: var(--primary-aux-color);
      font-size: 0.8em;
      font-weight: 400;
}




#footer {
    text-align: center;
    font-size: 0.9em;
    padding-top: 1em;
}

#footer .media {
    margin: 0;
}

#footer hr {
    margin-bottom: 0.8em;
    width: 100%;
    height: 1px;
    border: 0;
    background: var(--secondary-aux-color);
    box-shadow: 0px 1px 1px 1px lightgrey;
}

#footer .media-item {
    padding: 0;
    padding-left: 1em;
}

#footer > div {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 0.5em;
    align-items: center;
}

#footer p {
    display: inline-block;
    justify-self: flex-start;
}

:root {
    --secondary-font:       'Poppins', sans-serif;
    --primary-font:         'Raleway', sans-serif;
    --secondary-bg-color:   #FEFDFE;
    --primary-bg-color:     #EBEDEE;
    --primary-font-color:   #133B5C;
    --secondary-font-color: #422F55;
    --primary-aux-color:    #0FA08A;
    --secondary-aux-color:  #E6B184;
    --picture-size:         140px;
    font-size:              16px;
}

/* disable scrollbar */
html, body {
    overflow-x: hidden;
    overflow-y: scroll;
    scrollbar-width: none; /* Firefox */
    -ms-overflow-style: none;  /* Internet Explorer 10+ */
    scroll-behavior: smooth;
}

body::-webkit-scrollbar { /* WebKit */
    width: 0;
    height: 0;
}


* {
    padding: 0;
    margin: 0;
}

span, p {
    color: #422F55;
    color: var(--secondary-font-color);
}

ul, ol {
    list-style-type: none;
}

a,
a:link,
a:visited,
a:hover,
a:active {
  text-decoration: none;
  color: #133B5C;
  color: var(--primary-font-color);
}

body {
    font-size:   20px;
    padding:     1em;
    font-weight: 300;
    width:       100%;
    font-family: 'Raleway', sans-serif;
    font-family: var(--primary-font);
    box-sizing:  border-box;
    background:  #EBEDEE;
    background:  var(--primary-bg-color);
}

.container {
    box-shadow:     rgba(0, 0, 0, 0.35) 0px 5px 15px;
    border-radius:  0.4em;
    display:        flex;
    flex-direction: column;
    width:          100%;
    margin:         0 auto;
    background:     #FEFDFE;
    background:     var(--secondary-bg-color);
}

@media (min-width: 768px) {
    .container {
        width: 768px;
    }
}

@media (max-width: 576px) {
    body {
        padding: 0;
    }
    .container {
        border-radius: 0;
    }
}

